.navbar{
    box-sizing: border-box;
    width: 100%;
    height: 8vh;
    background: #121619;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;

}

.nav-links{
    display: flex;
    justify-content: flex-end;
    list-style-type: none;
    width: 70%; 
}
 img{
    height: 80%;
}
li{
    text-decoration: none;
    color: #e1dfba;
    font-size: 20px;
    padding: 10px 20px;
    margin: 0 10px;
    cursor: pointer;
    transition: all 1s linear;
}


.navbar a{
    color: #fff;
    text-decoration: none;
    
}

.navbar-right button{
    background: transparent;
    border: none;
    color: #fff;
    cursor: pointer;
    display: none;
}
.mobile-menu-icon{
    display: none;
}

@media screen and (max-width:780px){
    .navbar{
       
       
    }
    .nav-links{
        display: none;
        
    }
    ul.nav-links-mobile{
        padding-left: 0;
        z-index: 20;
    }
    .nav-links-mobile{
        position: absolute;
        display: block;
        list-style: none;
        background-color: #2c3133;
        border-left-width: 0;
        top: 55px;
        transition: all 0.5s ease-out;
        width: 100%;
        
        
    }
    li{
        color: white;
        text-align: center;
        padding: 32px;
      
        transition:  all 0.5s ease;
        margin: 0 0 0 0 ;
        
    }
    li:hover{
        border-bottom: none; 
    }
    .mobile-menu-icon{
        display: block;
        position: absolute;
        font-size: 30px;
        color: white;
       background-color: transparent;
        border: none;
        outline: none;
        top: 15px;
        right: 25px;
    }
}